.client {
  padding-top: 94px;
  padding-bottom: 127px;

  background-color: $primary-light-color;
  border-radius: 200px 0px 0px 0px;
}

.client__row {
  display: flex;
  margin-bottom: 111px;
}

.client__caption {
  margin: 0;
  max-width: 230px;

  color: $white;
}

.client__text {
  max-width: 540px;
  margin: 0 auto;

  font-family: $gilroy;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.65;
  letter-spacing: 0.05em;
  color: $white;
  opacity: 0.6;
}

.client__list {
  @include list();

  display: flex;
  flex-wrap: wrap;
}

.client__link,
.client__item {
  font-family: $gilroy;
  font-size: 20px;
  line-height: 2.3;
  letter-spacing: 0.02em;
  color: $white;
}

.client__item {
  min-height: 128px;
  width: 25%;
  display: flex;
  align-items: center;
  justify-content: center;
  border-bottom: 1px solid $white;
  border-right: 1px solid $white;

  &--link {
    align-items: stretch;
    background-color: $primary-opacity-color;
  }

  &:nth-child(n+5) {
    border-bottom: none;
  }

  &:nth-child(4n) {
    border-right: none;
  }
}

.client__link {
  @include hover();
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  min-height: 100%;
  text-decoration: none;
}
